import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import {BiAlarm} from 'react-icons/bi'
import { FaBars, FaTimes } from 'react-icons/fa'
import { Button } from './Button'
import './Navbar.css'
import { IconContext } from 'react-icons/lib'

function Navbar() {
    const[click, setClick] = useState(false); 
    const[button, setButton] = useState(true);

    const handleClick = () => setClick(!click);

    const closeMobileMenu = () => setClick(false)

    const showButton = () => {
        if(window.innerWidth <= 960){
            setButton(false)
        } else {
            setButton(true)
        }
    }

    useEffect(() => {
        showButton();
    }, []);

    window.addEventListener('resize', showButton);

    return (
        <>
            <IconContext.Provider value = {{color: '#fff'}}>
                <div className = "navbar">
                    <div className = "navbar-container container">
                        <Link to = '/' className = "navbar-logo" onClick = {closeMobileMenu }>
                            <BiAlarm className = 'navbar-icon'/>
                            TK
                        </Link>
                        <div className = "menu-icon" onClick = {handleClick}>
                            {click ? <FaTimes /> : <FaBars />}
                        </div>
                        <ul className = {click ? 'nav-menu active' : 'nav-menu'}>
                            <li className = "nav-item">
                                <Link to = '/' className = "nav-links" onClick = {closeMobileMenu }>
                                    Home
                                </Link>
                            </li>
                            <li className = "nav-item">
                                <Link to = '/Reminder' className = "nav-links" onClick = {closeMobileMenu }>
                                    Reminder
                                </Link>
                            </li>
                            <li className = "nav-item">
                                <Link to = '/Timer' className = "nav-links" onClick = {closeMobileMenu }>
                                    Timer
                                </Link>
                            </li>
                            <li className = "nav-item">
                                <Link to = '/Calendar' className = "nav-links" onClick = {closeMobileMenu }>
                                    Calendar
                                </Link>
                            </li>

                            <li className = 'nav-button'>
                                {button ? (
                                    <Link to = '/sign-up' className = 'button-link'>
                                        <Button buttonStyle = 'button--outline'>
                                            Want More!
                                        </Button>
                                    </Link>
                                ) : (
                                    <Link to = '/sign-up' className = 'button-link' onClick = {closeMobileMenu }>
                                        <Button buttonStyle = 'button--outline' buttonSize = 'button--mobile'>
                                            Want More!
                                        </Button>
                                    </Link>
                                )}
                            </li>
                        </ul>
                    </div>
                </div>  
            </IconContext.Provider>
        </>
    )
}

export default Navbar
